// material
import { experimentalStyled as styled } from '@material-ui/core/styles';
import InsScOne from 'components/_external-pages/initiatives/InsScOne';
// components
import Page from '../components/Page';
import {
  LandingHero, NftSectionOne, NftSecThree, NftScTwo,  
  
} from '../components/_external-pages/nftree';

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  height: '100%'
}));

const ContentStyle = styled('div')(({ theme }) => ({
  overflow: 'hidden',
  position: 'relative',
  backgroundColor: theme.palette.background.default
}));

// ----------------------------------------------------------------------

export default function NFTree() {
  return (
    <RootStyle title="NFTree - Treedefi" id="move_top">
      <LandingHero />
        <ContentStyle>
            <NftSectionOne />
            <NftScTwo />
            <NftSecThree />
        </ContentStyle>
    </RootStyle>
  );
}

