import { alpha, useTheme, experimentalStyled as styled } from '@material-ui/core/styles';
import { Box, Grid, Card, Container, Typography, useMediaQuery } from '@material-ui/core';
import { varFadeInUp, MotionInView, varFadeInDown } from '../../animate';

export default function NftSectionOne() {
    return (
        <>
            <Box sx={{ pt: {xs: 10, lg: 5 }, pb: {xs: 5, lg: 15 }  }}>
                <Container maxWidth="lg">
                    <Grid container spacing={2}>
                        <Grid item xs={12} className="pl_10">
                            <MotionInView variants={varFadeInDown}>
                                <Typography variant="h2">
                                NFTree Marketplace
                                </Typography>
                                <Typography component="p" className="sb_ttl_txt text-left ml-0" sx={{ mb: {xs: 3, lg: 5 }, }}>
                                    Our marketplace offers various features to enrich our user's NFT trading experience.                                </Typography>
                            </MotionInView>
                        </Grid>
                        <Grid item xs={12} md={4} >
                            <MotionInView variants={varFadeInUp}>
                                <Box className="nftr_i_ttx">
                                    <Typography component="p">Have freedom while listing your NFTrees through our Dutch and English auction systems.</Typography>
                                </Box>
                            </MotionInView>
                        </Grid>
                        <Grid item xs={12} md={4} >
                            <MotionInView variants={varFadeInUp}>
                                <Box className="nftr_i_ttx">
                                    <Typography component="p">Sort the NFTrees you're looking for easily through our filters for categories, country of origin and CO2 offset.</Typography>
                                </Box>
                            </MotionInView>
                        </Grid>
                        <Grid item xs={12} md={4} >
                            <MotionInView variants={varFadeInUp}>
                                <Box className="nftr_i_ttx">
                                    <Typography component="p">Your NFTrees will continuously generate CO2 tokens, allowing you to create CO2 offset certificates.</Typography>
                                </Box>
                            </MotionInView>
                        </Grid>
                    </Grid>
                </Container>
            </Box>
        </>
    );
}

